@import url(https://fonts.googleapis.com/css?family=Lato:300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



body{position:relative;background-color:#101c22}.box{display:inline-block;border:0.3px solid #1b1b1b;width:10px;height:10px;margin-left:0px;margin-bottom:0px;background-color:black}.btn{background-color:#000;color:white;height:40px;width:100px;border-radius:16px;border:1px solid red;outline:none}.btn:active{background-color:#fff;color:#000;height:40px;width:100px;border-radius:16px;border:1px solid red}.off{background-color:#000}.on{background-color:#1eff00}.box:hover{background:#5856c2}.center{margin:auto;display:flex;margin-top:10px;width:940px;height:50px;justify-content:space-between}#size-menu{height:40px;width:100px}.buttontoolbar{width:940px;display:flex;justify-content:space-between}.dropdown{height:50px;width:100px;display:flex}.grid{display:flex;flex-wrap:wrap;width:150px;line-height:0;margin:auto;margin-top:20px}h1,h2,h3,h4{color:white;margin:auto;text-align:center;width:50%;margin-top:10px;font-family:fantasy}.rules{display:flex;flex-direction:column;width:940px}.rules-header{text-align:left;margin-left:20px}.rules-list{color:white}.bio{color:white;margin-left:20px}.john{width:250px;height:162.5px;margin-left:20px;margin-top:20px;border-radius:40px}h4{margin-top:0px}

body{display:flex;justify-content:center;align-items:center;margin-top:50px;background:#222}.gradient-border{--border-width: 4px;position:relative;margin:0 auto;font-family:Lato, sans-serif;font-size:2.5rem;text-transform:uppercase;color:white;background:#222;border-radius:var(--border-width);margin-bottom:40px}.gradient-border::after{position:absolute;content:"";top:calc(-1 * var(--border-width));left:calc(-1 * var(--border-width));z-index:-1;width:calc(100% + var(--border-width) * 2);height:calc(100% + var(--border-width) * 2);background:linear-gradient(60deg, #5f86f2, #a65ff2, #f25fd0, #f25f61, #f2cb5f, #abf25f, #5ff281, #5ff2f0);background-size:300% 300%;background-position:0 50%;border-radius:calc(2 * var(--border-width));-webkit-animation:moveGradient 4s alternate infinite;animation:moveGradient 4s alternate infinite}@-webkit-keyframes moveGradient{50%{background-position:100% 50%}}@keyframes moveGradient{50%{background-position:100% 50%}}

